import { NewGameState } from '../core/game-engine';
import { Card } from '../types/poker';

// ğŸ“Š å¤æ‚åº¦è¯„ä¼°ç»“æœ
export interface ComplexityAssessment {
    totalScore: number;        // æ€»å¤æ‚åº¦åˆ†æ•° (0-100)
    category: 'simple' | 'medium' | 'complex' | 'extreme';
    factors: {
      handStrengthAmbiguity: number;    // ç‰ŒåŠ›æ¨¡ç³Šåº¦ (0-25)
      opponentRangeWidth: number;       // å¯¹æ‰‹èŒƒå›´å®½åº¦ (0-25) 
      actionSequenceComplexity: number; // è¡ŒåŠ¨åºåˆ—å¤æ‚åº¦ (0-25)
      stackAndPotDynamics: number;      // ç­¹ç å’Œåº•æ± åŠ¨æ€ (0-25)
    };
    recommendedTimeout: number;         // æ¨èè¶…æ—¶æ—¶é—´(ms)
    recommendedTemperature: number;     // æ¨èAIæ¸©åº¦
    promptType: 'minimal' | 'standard' | 'detailed' | 'comprehensive';
  }

// ğŸ¯ å±€åŠ¿å¤æ‚åº¦åˆ†æå™¨
export class SituationComplexityAnalyzer {

  // ğŸ¯ ä¸»è¦åˆ†æå…¥å£
  analyzeSituation(
    gameState: NewGameState,
    playerId: string,
    holeCards: Card[]
  ): ComplexityAssessment {

    // ğŸš€ è°ƒè¯•ï¼šæ£€æŸ¥gameState.phase
    console.log('ğŸ” SituationComplexityAnalyzeræ”¶åˆ°çš„gameState:', {
      phase: gameState.phase,
      phaseType: typeof gameState.phase,
      gameStateKeys: Object.keys(gameState),
      hasPhase: 'phase' in gameState,
      gameStateStructure: {
        gameId: gameState.gameId,
        phase: gameState.phase,
        players: gameState.players?.length,
        pot: gameState.pot
      }
    });

    // ç®€åŒ–å®ç°ï¼šåŸºäºæ¸¸æˆé˜¶æ®µçš„åŸºç¡€å¤æ‚åº¦è¯„ä¼°
    let baseScore = 0;

    // æ ¹æ®æ¸¸æˆé˜¶æ®µè®¾ç½®åŸºç¡€åˆ†æ•°
    switch (gameState.phase) {
      case 'preflop':
        baseScore = 20; // ç¿»å‰ç›¸å¯¹ç®€å•
        break;
      case 'flop':
        baseScore = 35; // ç¿»ç‰Œä¸­ç­‰å¤æ‚
        break;
      case 'turn':
        baseScore = 50; // è½¬ç‰Œè¾ƒå¤æ‚
        break;
      case 'river':
        baseScore = 65; // æ²³ç‰Œæœ€å¤æ‚
        break;
      default:
        baseScore = 30;
    }

    // æ ¹æ®åº•æ± å¤§å°è°ƒæ•´
    const pot = gameState.pot || 0;
    const bigBlind = gameState.bigBlind || 100;
    const potBBs = pot / bigBlind;

    if (potBBs > 20) baseScore += 15; // å¤§åº•æ± å¢åŠ å¤æ‚åº¦
    else if (potBBs > 10) baseScore += 8;
    else if (potBBs > 5) baseScore += 3;

    // æ ¹æ®å¯¹æ‰‹æ•°é‡è°ƒæ•´
    const activeOpponents = gameState.players.filter(p => p.isActive && p.id !== playerId).length;
    if (activeOpponents >= 3) baseScore += 10; // å¤šäººåº•æ± 
    else if (activeOpponents >= 2) baseScore += 5;

    const totalScore = Math.min(baseScore, 100);

    const factors = {
      handStrengthAmbiguity: Math.floor(totalScore * 0.3),
      opponentRangeWidth: Math.floor(totalScore * 0.25),
      actionSequenceComplexity: Math.floor(totalScore * 0.25),
      stackAndPotDynamics: Math.floor(totalScore * 0.2)
    };

    return {
      totalScore,
      category: this.categorizeComplexity(totalScore),
      factors,
      recommendedTimeout: this.getRecommendedTimeout(totalScore),
      recommendedTemperature: this.getRecommendedTemperature(totalScore),
      promptType: this.getPromptType(totalScore)
    };
  }

  // ğŸ·ï¸ å¤æ‚åº¦åˆ†ç±»
  private categorizeComplexity(score: number): 'simple' | 'medium' | 'complex' | 'extreme' {
    if (score <= 25) return 'simple';
    if (score <= 50) return 'medium';
    if (score <= 75) return 'complex';
    return 'extreme';
  }

  // â±ï¸ æ¨èè¶…æ—¶æ—¶é—´ (ä¸´æ—¶æ”¾å®½é™åˆ¶ç¡®ä¿ç¨³å®šæ€§)
  private getRecommendedTimeout(score: number): number {
    if (score <= 25) return 15000;  // 15ç§’ - ç®€å•å±€åŠ¿
    if (score <= 50) return 25000;  // 25ç§’ - ä¸­ç­‰å¤æ‚åº¦
    if (score <= 75) return 35000;  // 35ç§’ - å¤æ‚å±€åŠ¿
    return 45000; // 45ç§’ - æå¤æ‚å±€åŠ¿
  }

  // ğŸŒ¡ï¸ æ¨èAIæ¸©åº¦
  private getRecommendedTemperature(score: number): number {
    if (score <= 25) return 0.1;   // ç®€å•å±€åŠ¿ï¼Œä½æ¸©åº¦
    if (score <= 50) return 0.3;   // ä¸­ç­‰å¤æ‚ï¼Œä¸­ç­‰æ¸©åº¦
    if (score <= 75) return 0.5;   // å¤æ‚å±€åŠ¿ï¼Œè¾ƒé«˜æ¸©åº¦
    return 0.7; // æå¤æ‚ï¼Œé«˜æ¸©åº¦ä¿æŒåˆ›é€ æ€§
  }

  // ğŸ“ æ¨èPromptç±»å‹
  private getPromptType(score: number): 'minimal' | 'standard' | 'detailed' | 'comprehensive' {
    if (score <= 25) return 'minimal';
    if (score <= 50) return 'standard';
    if (score <= 75) return 'detailed';
    return 'comprehensive';
  }
}
